import { makeTree } from '@/util/utils'
const MODULE_USER = {
  state: {
    depttree: [
      {
        deptId: 1,
        deptName: 'A',
        parentId: 0,
        open: true,
        children: [
          {
            deptId: 2,
            deptName: '2',
            parentId: 1,
            open: true,
            children: [
              {deptId: 3, deptName: '3', parentId: 2, open: true}
            ]
          }
        ]
      },
      {
        deptId: 4,
        deptName: '4',
        parentId: 0,
        open: true,
        children: [
          {deptId: 5, deptName: '5', parentId: 4, open: true},
          {deptId: 6, deptName: '6', parentId: 4, open: true}
        ]
      },
      {deptId: 7, deptName: '7', parentId: 0, open: false},
      {deptId: 8, deptName: '8', parentId: 0, open: true}
    ], // 
    deptdata: [
      {deptId: 1, deptName: 'A', parentId: 0, open: true},
      {deptId: 2, deptName: '2', parentId: 1, open: true},
      {deptId: 3, deptName: '3', parentId: 2, open: true},
      {deptId: 4, deptName: '4', parentId: 0, open: true},
      {deptId: 5, deptName: '5', parentId: 4, open: true},
      {deptId: 6, deptName: '6', parentId: 4, open: true},
      {deptId: 7, deptName: '7', parentId: 0, open: false},
      {deptId: 8, deptName: '8', parentId: 0, open: true}
    ]
  },
  getters: {
    DEPT_TREE: state => user => {
      return {
        code: 200,
        msg: 'success',
        data: state.depttree
      }
    },
    DEPT_ADD: (state) => menu => {
      let k = 0
      for (let i = 0; i < state.deptdata.length; i++) {
        if (k <= state.deptdata[i].deptId) {
          k = state.deptdata[i].deptId + 1
        }
      }
      menu.deptId = k
      state.deptdata.push(menu)
      console.log(menu)
      const treeData = makeTree(state.deptdata, 0, {
        id: 'deptId',
        pid: 'parentId',
        child: 'children'
      })
      state.depttree = treeData
      return {
        msg: '',
        code: 200,
        data: true
      }
    },
    DEPT_DETAIL: (state) => dept => {
      let menus = state.data.find(item => item.deptId === dept.deptId)
      return {
        msg: '',
        code: 200,
        data: menus
      }
    },
    DEPT_UPDATE: (state) => menu => {
      let menus = state.deptdata.find(item => item.deptId === menu.deptId)
      state.deptdata[state.deptdata.indexOf(menus)] = menu
      const treeData = makeTree(state.deptdata, 0, {
        id: 'deptId',
        pid: 'parentId',
        child: 'children'
      })
      state.depttree = treeData
      return {
        msg: '',
        code: 200
      }
    },
    DEPT_DEL: (state) => menu => {
      let menus = state.deptdata.find(item => item.deptId === menu.deptId)
      state.deptdata.splice(state.deptdata.indexOf(menus), 1)
      const treeData = makeTree(state.deptdata, 0, {
        id: 'deptId',
        pid: 'parentId',
        child: 'children'
      })
      console.log(treeData)
      state.depttree = treeData
      return {
        msg: '',
        code: 200
      }
    }
  },
  mutations: {
  }
}
export default MODULE_USER



